.menu__hamburger {
  width: 78px;
  height: 78px;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
  font-size: 0;
  transition: background .3s;
  display: block;
  position: relative;
  overflow: hidden;
}

.menu__hamburger span {
  height: 5px;
  background: #fdcc52;
  display: block;
  position: absolute;
  top: 40px;
  left: 20px;
  right: 20px;
}

.menu__hamburger span:after, .menu__hamburger span:before {
  width: 100%;
  height: 5px;
  content: "";
  background-color: #fff;
  display: block;
  position: absolute;
  left: 0;
}

.menu__hamburger span:before {
  top: -15px;
}

.menu__hamburger span:after {
  bottom: -15px;
}

.menu__hamburger--htx span {
  transition: background 0s .3s;
}

.menu__hamburger--htx span:before, .menu__hamburger--htx span:after {
  transition-duration: .3s, .3s;
  transition-delay: .3s, 0s;
}

.menu__hamburger--htx span:before {
  transition-property: top, transform;
}

.menu__hamburger--htx span:after {
  transition-property: bottom, transform;
}

.menu__hamburger.is-active span {
  background: none;
}

.menu__hamburger--htx.is-active span:before {
  top: 0;
  transform: rotate(45deg);
}

.menu__hamburger--htx.is-active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}

.menu__hamburger--htx.is-active span:after, .menu__hamburger--htx.is-active span:before {
  transition-delay: 0s, .3s;
}

